export default class Stars {
	constructor(ctx, width, height, amount) {
		this.ctx = ctx
		this.width = width
		this.height = height
		this.stars = this.getStars(amount)
	}

	//ËøîÂõû‰∏ÄÂ†ÜÁöÑÂ∞èÊòüÊòü
	getStars(amount) {
		let stars = []
		while(amount--) {
			// console.log(amount)
			// ÂàõÂª∫‰∏Ä‰∏™ÊòüÊòüÁöÑÂùêÊ†áÂèäÂ§ßÂ∞èÊï∞ÊçÆÊèèËø∞JSONÂØπË±°ÔºåÊ∑ªÂä†Âà∞starsÊï∞ÁªÑ
			stars.push({
				x: Math.random()*this.width,
				y: Math.random()*this.height,
				r: Math.random()+0.2
			})
		}
		return stars
	}

	//ÁîªÊª°Â§©Êòü
	draw() {
		let ctx = this.ctx
		ctx.save()   //‰øùÂ≠òcanvasÂΩìÂâçÁªòÂà∂Ôºå‰∏ÄËà¨Êñ∞ÁªòÂà∂ÂâçË∞ÉÁî®
		ctx.fillStyle = 'white'
		//star ÊòØ‰ªéstarsÈáåÁöÑ‰∏ÄÈ¢óstar ËøôÈáåÊòØfunctionÁöÑÂèÇÊï∞
		this.stars.forEach(star => {
			ctx.beginPath()  //ÂºÄÂßãÁªòÂà∂
			ctx.arc(star.x, star.y, star.r, 0, 2*Math.PI)  //ÁîªÊ§≠ÂúÜ
			ctx.fill()  //Â°´ÂÖÖËâ≤
		})
		ctx.restore() //ÂÜçÊ¨°‰øùÂ≠ò
	}

	//ÊòüÊòüÊ≤°Èöî10Â∏ßÈó™‰∏Ä‰∏ã
	blink() {
		//mapÊñπÊ≥ï ÊâæÂà∞ÂêàÈÄÇÁöÑÊòüÊòüÊîæËøõÊñ∞Êï∞ÁªÑ ÂåπÈÖç
		this.stars = this.stars.map(star => {
			let sign = Math.random()>0.5 ? 1 : -1
			star.r += sign * 0.2
			if (star.r < 0) {
				star.r = -star.r
			} else if (star.r > 1){
				star.r -= 0.2
			// console.log(star.r)

			}
			return star
		})
	}
}