<template>
  <div id="menu">
    <canvas id="LxCanvas" class="LxCanvas"></canvas>
  </div>
</template>

<script>

import Stars from './Star'
import Moon from './Moon'
import Meteor from './Meteor'

export default {
  mounted() {
    let canvas = document.getElementById('LxCanvas'),
      ctx = canvas.getContext('2d'),
      width = window.innerWidth,
      height = window.innerHeight,
      //ÂÆû‰æãÂåñÊúà‰∫ÆÂíåÊòüÊòü„ÄÇÊµÅÊòüÊòØÈöèÊú∫Êó∂Èó¥ÁîüÊàêÔºåÊâÄ‰ª•Âè™ÂàùÂßãÂåñÊï∞ÁªÑ
      moon = new Moon(ctx, width, height),
      stars = new Stars(ctx, width, height, 1500),
      meteors = [],
      count = 0

    canvas.width = width;
    canvas.height = height;

    const meteorGenerator = () => {
      let x = Math.random() * 4 * width 
      meteors.push(new Meteor(ctx, x, height))

      //ÊØèÈöîÈöèÊú∫Êó∂Èó¥ÔºåÁîüÊàêÊñ∞ÊµÅÊòü
      setTimeout(() => {
        meteorGenerator()

      }, Math.random() * 500)
    }

    const frame = () => {
      count++
      count % 10 == 0 && stars.blink()
      moon.draw()
      stars.draw()

      meteors.forEach((meteor, index, arr) => {
        //Â¶ÇÊûúÊµÅÊòüÁ¶ªÂºÄËßÜÈáé‰πãÂÜÖÔºåÈîÄÊØÅÊµÅÊòüÂÆû‰æãÔºåÂõûÊî∂ÂÜÖÂ≠ò
        if (meteor.flow()) {
          meteor.draw()
        } else {
          arr.splice(index, 1)
        }
      })
      requestAnimationFrame(frame)
    }
    meteorGenerator()
    frame()
  }
}
</script>

<style scoped>
#menu {
  height: 100%;
  overflow: hidden;
  position: relative;
}

.LxCanvas {
  position: fixed;
  /* z-index: -1; */
  z-index: 9999;
  opacity: .2;
  pointer-events: none;
}
</style>